import { useReducer, useState } from "react"
import { appFireStore, timestamp, storage } from "../firebase/config"
import { addDoc, updateDoc, deleteDoc, doc, collection } from "firebase/firestore"
import { ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";
import { GetCurDayTime, GetUniqueNum }  from "../utils/DateUtil.js"


// ìš°ë¦¬ê°€ ë°›ì„ ì‘ë‹µì„ ì €ì¥í•  ê°ì²´ (ê°ì²´ì´ê¸° ë•Œë¬¸ì— ë¦¬ë“€ì„œë¡œ ê´€ë¦¬)
// ìƒíƒœë¥¼ ê´€ë¦¬í•  ë•Œ errorë‚˜ isPendingì„ useReducerë¡œ í•œë²ˆì— ê´€ë¦¬

/**
 * document : íŒŒì´ì–´ìŠ¤í† ì–´ì— documentì˜ ìƒì„±ì„ ìš”ì²­í•˜ë©´ ìš°ë¦¬ê°€ ìƒì„±í•œ documentë¥¼ ë°˜í™˜
 *            íŒŒì´ì–´ìŠ¤í† ì–´ì˜ ë°ì´í„° ì €ì¥ ë‹¨ìœ„
 * isPending: í†µì‹ ì¤‘ì¸ì§€ ì•„ë‹Œì§€ ìƒíƒœ
 * success : ìš”ì²­ì— ëŒ€í•œ ì‘ë‹µì˜ ì„±ê³µ ìœ ë¬´
 */
const initState = {
    document: null,
    isPending: false,
    error: null,
    success: false
}

// ì „ë‹¬ ë°›ëŠ” actionì— ë”°ë¥¸ state ì—…ë°ì´íŠ¸
const storeReducer = (state, action) => {
    /**case ë§ˆë‹¤ ë°ì´í„°ê°€ ë‹¤ ë‹¬ë¼ì„œ spread operator ì•ˆì“°ê³  ê·¸ëƒ¥ ì ìŒ... */
    switch (action.type) {
        case 'isPending':
            return { isPending: true, document: null,            success: false, error: null }
        case 'addDoc':
            return { isPending: false, document: action.payload, success: true,  error: null }
        case 'editDoc':
            return { isPending: false, document: action.payload, success: true,  error: null }
        case 'deleteDoc':
            return { isPending: false, document: action.payload, success: true,  error: null }
        case 'error':
            return { isPending: false, document: null,           success: false, error: action.payload }
        default:
            return state
    }
}

// transaction: ìš°ë¦¬ê°€ ë°ì´í„°ë¥¼ ì €ì¥í•  ì»¬ë ‰ì…˜(í´ë”)
export const useFirestore = (transaction) => {

    // responseì— ìš”ì²­ì— ëŒ€í•œ firestore ì˜ ì‘ë‹µ ì €ì¥
    // ì €ì¥ë˜ëŠ” ë°ì´í„° === ì €ì¥ ì„±ê³µ ë˜ëŠ” ìš”ì²­í•œ ë¬¸ì„œ ë°ì´í„°(ê°ì²´)
    const [response, dispatch] = useReducer(storeReducer, initState);
    const [imgUrl, setImgUrl] = useState([]);
    // colRef : ë§Œë“¤ ì»¬ë ‰ì…˜ì˜ ì°¸ì¡° (ì»¬ë ‰ì…˜ ì´ë¦„)
	// ì›í•˜ëŠ” ì»¬ë ‰ì…˜ì˜ ì°¸ì¡°ë¥¼ ì¸ìë¡œ ë³´ë‚´ì£¼ë©´ íŒŒì´ì–´ìŠ¤í† ì–´ê°€ ìë™ìœ¼ë¡œ í•´ë‹¹ ì»¬ë ‰ì…˜ì„ ìƒì„±í•´ì¤Œ 
    const colRef = collection(appFireStore, transaction);

    
    // ì»¬ë ‰ì…˜ì— ë¬¸ì„œë¥¼ ì €ì¥(ì´ë¯¸ì§€ ì €ì¥ ì‹œ)
    const addDocument = async (doc,pic) => {

        // ì‹œê°„ ì €ì¥(order by ìš©)
        const createdTime = timestamp.fromDate(new Date());
        const createdDate = GetCurDayTime('/',':');

        // ìœ ì¼í‚¤ ì €ì¥
        const createdUqe = GetUniqueNum();

        // ì´ë¯¸ì§€ ì—…ë¡œë“œ ê²½ë¡œ ì €ì¥
        const storageRef = ref(storage, 'images/'+pic.name );
        const uploadTask = uploadBytesResumable(storageRef, pic);


        dispatch({ type: "isPending" });
        try {

            /*===============================================
             * ì´ë¯¸ì§€ ì €ì¥
             *===================================================*/

            uploadTask.on('state_changed', 
            (snapshot) => {
                null
            }, 
            (error) => {
                console.error('ì‹¤íŒ¨ì‚¬ìœ ëŠ”', error);
            }, 
            () => {
              getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
                
                console.log('ì—…ë¡œë“œëœ ê²½ë¡œëŠ”', downloadURL);
                /*===============================================
                * ë°ì´í„° ì €ì¥
                *===================================================*/
                // docRef : ì°¸ì¡°(ì»¬ë ‰ì…˜ ì´ë¦„)
                // addDoc : ì»¬ë ‰ì…˜ì— ë¬¸ì„œë¥¼ ì¶”ê°€
                const docRef = addDoc(colRef,{ ...doc, createdTime, createdDate,createdUqe, downloadURL});
                console.log(docRef);

                dispatch({ type: 'addDoc', payload: docRef });
                console.log('ì €ì¥ì™„ë£Œ');             
              });
            }
          );

        } catch (error) {
            dispatch({ type: 'error', payload: error.message });
        }

    }


    // ì»¬ë ‰ì…˜ì— ë¬¸ì„œë¥¼ ì €ì¥(ëŒ“ê¸€ ì €ì¥)
    const addComment = async (doc) => {

        // ì‹œê°„ ì €ì¥(order by ìš©)
        const createdTime = timestamp.fromDate(new Date());
        const createdDate = GetCurDayTime('/',':');

        // ìœ ì¼í‚¤ ì €ì¥
        const createdUqe = GetUniqueNum();


        dispatch({ type: "isPending" });
        try {

                /*===============================================
                * ë°ì´í„° ì €ì¥
                *===================================================*/
                // docRef : ì°¸ì¡°(ì»¬ë ‰ì…˜ ì´ë¦„)
                // addDoc : ì»¬ë ‰ì…˜ì— ë¬¸ì„œë¥¼ ì¶”ê°€
                const docRef = addDoc(colRef,{ ...doc, createdTime, createdDate,createdUqe});
                console.log(docRef);

                dispatch({ type: 'addDoc', payload: docRef });
                console.log('ì €ì¥ì™„ë£Œ');               


        } catch (error) {
            dispatch({ type: 'error', payload: error.message });
        }

    }


    // ì»¬ë ‰ì…˜ì—ì„œ ë¬¸ì„œë¥¼ ìˆ˜ì •
    const editDocument = async (documents,id) => {
        dispatch({ type: "isPending" });
        
        // const ì„ì‹œ = doc(appFireStore,'diary',id);

        try {
            // // ìˆ˜ì • ë‚ ì§œê°€ í•„ìš”í•  ê²½ìš° ì‚¬ìš©í•˜ë©´ ë¨
            // const createdTime = timestamp.fromDate(new Date());
            // const createdDate = GetCurDayTime('/',':');

            // docRef : ì°¸ì¡°(ì»¬ë ‰ì…˜ ì´ë¦„)
            // updateDoc : ì»¬ë ‰ì…˜ì— ìˆëŠ” ë¬¸ì„œ ìˆ˜ì •
            // const docRef = await updateDoc(doc(colRef,id),{ ...documents, createdTime,createdDate});
            const docRef = await updateDoc(doc(colRef,id),{ ...documents});
            dispatch({ type: 'editDoc', payload: docRef });

        } catch (error) {
            dispatch({ type: 'error', payload: error.message });
        }
    }

        
    // ì»¬ë ‰ì…˜ì—ì„œ ë¬¸ì„œë¥¼ ì‚­ì œ
    const deleteDocument = async (id) => {

        dispatch({ type: "isPending" });
        try {
            const docRef = await deleteDoc(doc(colRef,id));
            dispatch({ type: 'deleteDoc', payload: docRef });
        } catch (error) {
            dispatch({ type: 'error', payload: error.message });
        }
    }

    
    // ìœ ì € ìµœì´ˆ ì €ì¥ ( íšŒì›ê°€ì… )
    const addUser = async (doc) => {

        // ì‹œê°„ ì €ì¥(order by ìš©)
        const createdTime = timestamp.fromDate(new Date());
        const createdDate = GetCurDayTime('/',':');

        // ìœ ì¼í‚¤ ì €ì¥
        const createdUqe = GetUniqueNum();


        dispatch({ type: "isPending" });
        try {

            /*===============================================
            * ë°ì´í„° ì €ì¥
            *===================================================*/
            // docRef : ì°¸ì¡°(ì»¬ë ‰ì…˜ ì´ë¦„)
            // addDoc : ì»¬ë ‰ì…˜ì— ë¬¸ì„œë¥¼ ì¶”ê°€
            const docRef = addDoc(colRef,{ ...doc, createdTime, createdDate,createdUqe});
            console.log(docRef);

            dispatch({ type: 'addDoc', payload: docRef });
            console.log('ì €ì¥ì™„ë£Œ');

        } catch (error) {
            dispatch({ type: 'error', payload: error.message });
        }

    }

    return { addDocument, addComment, editDocument, deleteDocument, addUser, response }

}