import { appFireStore } from "../firebase/config"
import { useEffect, useState } from "react"
import { onSnapshot,collection,query,where,orderBy } from "firebase/firestore";

export const useCollectionDtl = (transaction, myQuery, order="") => { // myQuery: íŒŒì´ì–´ìŠ¤í† ì–´ ì¿¼ë¦¬ ì‚¬ìš©ì„ ìœ„í•œ íŒŒë¼ë¯¸í„°

    // documents ë°ì´í„° ê´€ë¦¬, error ê´€ë¦¬
    const [documents, setDocuments] = useState(null);
    const [error, setError] = useState(null);

    // ğŸ’› collectionì— ë³€í™”ê°€ ìƒê¸¸ë•Œë§ˆë‹¤ ì‹¤í–‰í•©ë‹ˆë‹¤. ë•Œë¬¸ì— í•­ìƒ ìµœì‹ ì˜ ì»¬ë™ì…˜ ìƒíƒœë¥¼ ë°˜í™˜ ë°›ì„ ìˆ˜ ìˆìŠµë‹ˆë‹¤.
    useEffect(() => {
        let q;
        if(order.length>0){
            q = query(collection(appFireStore, transaction),where(...myQuery),orderBy("createdTime","desc"));
        }
        else{
            q = query(collection(appFireStore, transaction),where(...myQuery));
        }

        console.log(q);
        // onSnapshot: ê°€ì¥ ìµœê·¼ ì»¬ë™ì…˜ì˜ ë‚´ìš© ë°˜í™˜ 
        // unsubscribe: ë°ì´í„° ìˆ˜ì‹ ì„ ì¤‘ë‹¨(ë°ì´í„° ì˜¤ëŠ”ê±° ê¸°ë‹¤ë¦´ í•„ìš”ê°€ ì—†ì„ë•Œ ì‚¬ìš©.)
        const unsubscribe = onSnapshot(myQuery ? q : (collection(appFireStore, transaction)),
            
            // ì‘ë‹µë°›ì€ ì»¬ë™ì…˜ì„ snapshotì— ì €ì¥
            (snapshot) => {
                let result = [];
                // ë°°ì—´í˜•íƒœë¡œ ì €ì¥ë˜ì–´ìˆìŒ => forEach ì‚¬ìš©
                snapshot.docs.forEach((doc) => {
                    // document ë°ì´í„°ë‘ id ê°’ push í•´ì£¼ê¸°
                    result.push({ ...doc.data(), id: doc.id });
                })
                console.log(result);
                setDocuments(result);
                setError(null);
            },
            (error) => {
                setError(error.message);
            });
            
        //ğŸ’› ì™¸ë¶€ì—ì„œ ë°ì´í„°ë¥¼ êµ¬ë…í•˜ëŠ” ê²½ìš° clean-up í•¨ìˆ˜ëŠ” useEffectí›…ì„ ì‚¬ìš©í•˜ëŠ” ì»´í¬ë„ŒíŠ¸ê°€ ë§ˆìš´íŠ¸ í•´ì œë ë•Œ ì‹¤í–‰ë˜ì–´ êµ¬ë…ì„ ì¢…ë£Œí•˜ê²Œ ë©ë‹ˆë‹¤.
        return unsubscribe; // clean-up í•¨ìˆ˜

    }, [collection])// ë§¤ë²ˆ ì‹¤í–‰ë  í•„ìš”ê°€ ì—†ê³ , ì»¬ë™ì…˜ ë³€í™”ê°€ 
    return { documents, error }
}