/*eslint-disable */
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useAuthContext } from '../hooks/useAuthContext';
import { useCollectionDtl } from '../hooks/useCollectionDtl';
import { useFirestore } from "../hooks/useFirestore";
import { setPfVisible } from "../store/profileSlice";
import { Label } from "@material-ui/icons";
import { ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";
import { storage } from "../firebase/config";
import Loading from "../layout/Loading";


function ProfileInput(props) {
    const {user} = useAuthContext()
    const { documents : MyFeedInfo } = useCollectionDtl("FeedData",["UID","==",user.uid]) // firebaseì— ì €ì¥ëœ FeedData ì»¬ë ‰ì…˜ì—ì„œ ê°€ì ¸ì˜¨ë‹¤!
    const { documents : UserInfo } = useCollectionDtl("UserData",["UID","==",user.uid]) // firebaseì— ì €ì¥ëœ UserData ì»¬ë ‰ì…˜ì—ì„œ ê°€ì ¸ì˜¨ë‹¤!
    const { editDocument : FeedEdit } = useFirestore("FeedData");// ì»¬ë ‰ì…˜ ì´ë¦„ íŒŒë¼ë¯¸í„°ë¡œ ë„£ì–´ì£¼ê¸°
    const { editDocument : UserEdit } = useFirestore("UserData");// ì»¬ë ‰ì…˜ ì´ë¦„ íŒŒë¼ë¯¸í„°ë¡œ ë„£ì–´ì£¼ê¸°
    const pfVisible = useSelector((state) => state.profileState.pfVisible) // í”„ë¡œí•„ ë³€ê²½ ëª¨ë‹¬ í‘œì‹œ ì—¬ë¶€ ( profile change modal )
    let dispatch = useDispatch()
    let [showImg, setShowImg] = useState('') // ë¯¸ë¦¬ë³´ê¸° ì´ë¯¸ì§€
    let [saveImg, setSaveImg] = useState('') // ì‹¤ë¬¼ì €ì¥ ì´ë¯¸ì§€
    let [fade, setFade] = useState('') // Animation Style State
    let [newProfile, setNewProfile] = useState({
        displayName : '',
        profileImage: '',
        profileIntro: '',
    }); // ë³€ê²½í•  profile state
    let [imageChangeYN, setImageChangeYN] = useState(false) // í”„ë¡œí•„ì‚¬ì§„ ë³€ê²½ ì—¬ë¶€
    let [resultURL, setResultURL] = useState('') // ì—…ë¡œë“œ ê²°ê³¼ ì´ë¯¸ì§€ URL
    let [uploadYN, setUploadYN] = useState(false) // ì‚¬ì§„ ì—…ë¡œë“œ ì—¬ë¶€
    let [loading, setLoading] = useState(false) // ë¡œë”© ( ì—…ë¡œë“œ ì¤‘ì— ë³´ì—¬ì£¼ë„ë¡ )
    const { displayName, profileImage, profileIntro } = newProfile; // êµ¬ì¡°ë¶„í•´ í• ë‹¹ --> ê°’ ì¶”ì¶œ

    // OnChange
    const onChange = (e) => {
        const { value, name } = e.target; // ìš°ì„  e.target ì—ì„œ name ê³¼ value ë¥¼ ì¶”ì¶œ        
        if (e.target.type === 'text') {
            setNewProfile({
              ...newProfile, // ê¸°ì¡´ì˜ input ê°ì²´ë¥¼ ë³µì‚¬í•œ ë’¤
              [name]: value // name í‚¤ë¥¼ ê°€ì§„ ê°’ì„ value ë¡œ ì„¤ì •
            });
        }
        else {
            setPreviewImg(e)
        }
    };

    // ì´ë¯¸ì§€ onChange
    const setPreviewImg = (e) => {
        console.log("ë³€ê²½í•  íŒŒì¼ -->", e.target.files[0]);

        let reader = new FileReader();
        reader.onload = function(e) {
            // ë¯¸ë¦¬ë³´ê¸°ì— ë³´ì—¬ì¤„ state ë³€ê²½
            setShowImg(e.target.result);
        };
        reader.readAsDataURL(e.target.files[0]);
        setSaveImg(e.target.files[0]);
    }


    //=========================================================
    // í¼ì´ ì œì¶œë˜ë©´ ì‹¤í–‰ [í”„ë¡œí•„ ë³€ê²½]
    // Feed : displayName, profileImage ë³€ê²½
    // User : displayName, profileIntro, profileImage ë³€ê²½
    //=========================================================
    const onSubmit = (e) => {
        e.preventDefault(); // submitì‹œ í˜ì´ì§€ reload ë°©ì§€
        setLoading(true) // ë¡œë”© í‘œì‹œ

        console.log('ì‚¬ì§„ ë³€ê²½ ì—¬ë¶€ --> ',imageChangeYN)
        if (imageChangeYN) {
            /* ************************************
            *     í”„ë¡œí•„ ì‚¬ì§„ í¬í•¨ O ë³€ê²½
            *********************************** */
           // ì´ë¯¸ì§€ ì—…ë¡œë“œ ê²½ë¡œ ì €ì¥ & ê°€ì ¸ì˜¤ê¸°
            const storageRef = ref(storage, 'images/'+profileImage.name );
            const uploadTask = uploadBytesResumable(storageRef, profileImage); // ì„œë²„ì— ì—…ë¡œë“œ

            // ì €ì¥ë ë•Œê¹Œì§€ ì‹œê°„ì´ ê±¸ë¦¬ëŠ” ë“¯ í•¨ --> setTimeout ë“œê°€ì
            setTimeout(() => {
                getDownloadURL(uploadTask.snapshot.ref)
                .then((downloadURL) => {
                    console.log('downloadURL --> ', downloadURL);
                    // ì—…ë¡œë“œëœ ê²½ë¡œë¡œ state ë³€ê²½ --> **useEffect[resultURL] ë¡œ ì´ì–´ì§„ë‹¹**
                    setResultURL(downloadURL)
                })
                .catch((err)=> {
                    alert("ì„œë²„ì— ë¬¸ì œê°€ ë°œìƒí–ˆì–´ìš”ğŸ˜¥ \n ë‹¤ì‹œ ì‹œë„í•´ì£¼ì„¸ìš”!")
                    // ëª¨ë‹¬ ë„ê³ 
                    dispatch(setPfVisible(false))
                })
            }, 2000); // 1ì´ˆëŠ” ë¶€ì¡±í•˜ë‹¹..
        }
        else {
            /* ************************************
            *     í”„ë¡œí•„ ì‚¬ì§„ í¬í•¨ X ë³€ê²½
            *********************************** */
            if (MyFeedInfo.length !== 0) {
                MyFeedInfo.map((a,i)=>{
                    FeedEdit({ displayName }, a.id)
                })
            }
            UserEdit({ profileIntro, displayName }, UserInfo[0].id)
            .then(()=>{
                // ëª¨ë‹¬ ë„ê³ 
                dispatch(setPfVisible(false))
            })
        }

    }
    //=========================================================



    // í”„ë¡œí•„ ë³€ê²½ì‹œ URL ë³€ê²½ë˜ë©´ ( ì‚¬ì§„ O )
    useEffect(()=>{
        if (resultURL !== '' && resultURL !== undefined) {
            setNewProfile({
                ...newProfile,
                profileImage: resultURL
            })

            console.log("ìµœì¢… URL --> ",resultURL)
            setUploadYN(true); // ì‚¬ì§„ ì—…ë¡œë“œ ì—¬ë¶€ ì¶”ê°€
        }
    },[resultURL])

    // ì—…ë¡œë“œ, state ë³€ê²½ ì „ì²˜ë¦¬ ëë‚˜ë©´ --> **í”„ë¡œí•„ ìµœì¢… ë³€ê²½**
    useEffect(()=>{
        if (uploadYN) {
            console.log("í”„ë¡œí•„ ë³€ê²½ ( ì‚¬ì§„ O ) --> ",newProfile);
            // ê²Œì‹œë¬¼ì´ ìˆë‹¤ë©´
            if (MyFeedInfo.length !== 0) {
                MyFeedInfo.map((a,i)=>{
                    FeedEdit({ displayName, profileImage }, a.id)
                })
            }
            UserEdit({ profileIntro, displayName, profileImage }, UserInfo[0].id)
            .then(()=>{
                // ëª¨ë‹¬ ë„ê³ 
                dispatch(setPfVisible(false))
            })
        }
    },[uploadYN])

    // ì´ˆê¸° mount ì‹œ
    useEffect(()=>{
        setFade('transition-end')
        setShowImg('')
        setImageChangeYN(false)
        setUploadYN(false)
        setLoading(false)
    },[])
    
    // ì»¬ë ‰ì…˜ ë°ì´í„° ë°›ì•„ì˜¤ë©´ ì´ˆê¸°í™”
    useEffect(()=>{
        if (MyFeedInfo != null && UserInfo != null) {
            console.log("ê²Œì‹œê¸€ : ", MyFeedInfo)
            console.log("ì‚¬ìš©ì : ", UserInfo)
            
            // ë°›ì•„ì˜¬ ê²½ìš°ì— ì…‹íŒ…
            setNewProfile({
                displayName : UserInfo[0].displayName,
                profileImage: UserInfo[0].profileImage,
                profileIntro: UserInfo[0].profileIntro,
            })
        }
    },[UserInfo, MyFeedInfo])

    // í”„ë¡œí•„ ì´ë¯¸ì§€ê°€ ë³€ê²½ë˜ë©´!
    useEffect(()=>{
        setNewProfile({
            ...newProfile, // ê¸°ì¡´ì˜ input ê°ì²´ë¥¼ ë³µì‚¬í•œ ë’¤
            profileImage : saveImg // profileImageì— ì´ë¯¸ì§€ ì¶”ê°€ (file)
        });
        setImageChangeYN(true)
    },[saveImg])

    // ëª¨ë‹¬ on/off ì‹œ
    useEffect(()=>{
        setImageChangeYN(false)
        setUploadYN(false)
        setLoading(false)
    },[pfVisible])

    // Escë¡œ ëª¨ë‹¬ ë„ê¸°
    window.onkeydown = (e) => {
        if(e.key === 'Escape') {
            console.log('ê¾¸')
        }
    }
    window.onkeyup = (e) => { 
        if(e.key === 'Escape') {
            console.log('ìš±')
            dispatch(setPfVisible(false))
        }
    }

    return (
        <>
            {
                // ë¡œë”©ì¤‘
                loading ? 
                <Loading/>
                : null
            }
            {/* ë’¤ì— ìš”ì†Œë“¤ ë®ì–´ì„œ ëª¨ë‹¬ë§Œ ë³´ì´ê²Œ */}
            <div className={`dimmed-layer ${fade}`}/>

            <div className={`input-area transition-start ${fade}`}>
                <div className="form-container">
                    <form onSubmit={onSubmit}>
                        <div className="form-group profile-input">
                            <Label/>
                            <input accept="image/jpeg,image/png,image/heic,image/heif,video/mp4,video/quicktime" className="img-input profile-text-input" multiple="" type="file" onChange={onChange}/>
                        </div>
                        {/* ì´ë¯¸ì§€ ë¯¸ë¦¬ë³´ê¸° */}
                        { showImg.length > 0 && <img src={showImg ?? showImg} alt="" style={{marginBottom : '13px',width : '80%', maxHeight:'200px'}}/>}
                        
                        <div className="form-group profile-input">
                            <Label/>
                            <input type="text" name="displayName" value={displayName} onChange={onChange} className="form-control profile-text-input" placeholder="ë‹‰ë„¤ì„ì„ ì…ë ¥í•´ ì£¼ì„¸ìš”!" required/>
                        </div>

                        <div className="form-group profile-input">
                            <Label/>
                            <input type="text" name="profileIntro" value={profileIntro} onChange={onChange} className="form-control profile-text-input" placeholder="ì†Œê°œê¸€ì„ ì…ë ¥í•´ ì£¼ì„¸ìš”!" required/>
                        </div>
                        <input type="submit" className="post-btn" value="ì €ì¥í•˜ê¸°"/>
                        <div className="close-btn">
                            <CloseBtn onClick={(e)=>{
                                e.preventDefault()
                                dispatch(setPfVisible(false))
                            }}/>
                        </div>
                    </form>
                </div>
            </div>
        </>
    )
}

export default ProfileInput;


const CloseBtn = (props) => (
    <svg
        // width="800px"
        // height="800px"
        viewBox="0 0 16 16"
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        {...props}
    >
    <title>{"close"}</title>
    <path
      fill="#FFFFFF"
      d="M12.78 4.28a.75.75 0 00-1.06-1.06L8 6.94 4.28 3.22a.75.75 0 00-1.06 1.06L6.94 8l-3.72 3.72a.75.75 0 101.06 1.06L8 9.06l3.72 3.72a.75.75 0 101.06-1.06L9.06 8l3.72-3.72z"
    />
  </svg>
);